
//Stata
export const user = {
    userId:'USER_ID',
    userName:'USER_NAME',
}

// State
export const book = {
    addBook:'ADD_BOOK',
    deleteBook:'DELETE_BOOK',
}


//Action  => ID
export const userId = (text) => {
    return {
        type:user.userId,
        text
    }
 }
//Action  => 
export const userName = (text) => {
    return {
        type:user.userName,
        text
    }
}

//Action  => 
export const fetchUserName = (username) => {
    return dispatch => {
        // Promise 
        return new Promise((resolve,reject) => {
            setTimeout(() => {
                resolve(username);
            }, 1000);
        }).then(v => {
            dispatch(userName(v));
        })
    }
}




//
export const addBookAction = (text) => {
    return {
        type:book.addBook,
        text
    }
}

// 
export const deleteBookAction = (text) => {
    return {
        type:book.deleteBook,
        text
    }
}


